@import url("https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap");

:root {
  --bg-1: rgba(3, 3, 3, 0.842);
  --bg-2: rgba(18, 20, 19, 0.918);
  --c1: rgba(233, 247, 238, 0.774);
  --c2: rgb(245, 28, 28);
}

* {
  font-family: "Share Tech Mono", monospace;
}

html {
  scroll-behavior: smooth;
}

body {
  min-width: 100vh;
  background-color: var(--bg-1);
  color: var(--c1)
}

.navbar {
  background-color: var(--bg-2);
  margin-bottom: 20px;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 10;
  backdrop-filter: blur(4px);
}

.nav-item {
  font-weight: 500;
  margin-left: 16px;
  margin-top: 4px;
  margin-bottom: 4px;
  cursor: pointer;
}

.nav-item:hover,
.active {
  transform: scale(1.1);
  transition: 1ms all ease-in-out;
}

.active {
  font-weight: 600;
  border-bottom: 3px solid var(--c1);
}

.about-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 50px 0 50px;
  text-align: justify;
}

.about-container .img-container img {
  width: 100%;
  height: inherit;
  object-fit: cover;
  border-radius: 50%;
}

.img-container {
  width: 10rem;
  height: 10rem;
  transition: .5s all ease-in-out;
}

.img-container:hover {
  transform: scale(1.2) rotate(360deg);
  transition: .5s all ease-in-out;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  text-decoration: underline;
  font-size: 50px;
}

.title-first {
  color: var(--c1);
}

.title-second {
  color: var(--c2);
}

.dynamic-text {
  font-size: 50px;
  color: var(--c2);
}

.cursor {
  display: inline-block;
  background-color: var(--c1);
  animation: blinker .8s infinite;
  width: 5px;
}

@keyframes blinker {

  0%,
  100% {
    background-color: var(--c1);
  }

  50% {
    opacity: 0%;
  }
}

.skills-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 50px;
  gap: 40px;
}

.skill-img {
  padding: 10px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(5, 0, 0, 0.3);
  box-shadow: 0 8px 32px 0 rgba(135, 47, 31, 0.37);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border: 1px 0 solid rgba(255, 255, 255, 0.18);
  border-radius: 10px;
  width: 180px;
  height: 110px;
}

.skill-img img {
  max-width: 70px;
  object-fit: fill;
  transition: 0.2s all ease-in-out;
}

.skill-img img:hover {
  transform: scale(1.3);
}


@media (max-width: 768px) {
  .skills-container {
    margin: 10px;
    margin-top: 50px;
    gap: 15px;
  }

  .skill-img {
    width: 100px;
    height: 80px;
  }

  .skill-img img {
    max-width: 50px;
  }
}

.footer {
  width: 100%;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  background-color: rgb(33, 37, 41);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  color: var(--c1);
  padding-top: 16px;
  padding-bottom: 1px;
}

a {
  color: inherit;
}

img {
  width: 100%;
}

.project-container {
  display: grid;
  grid-template-columns: repeat(2, .33fr);
  /* display: flex;
  flex-direction: row;
  flex-wrap: wrap; */
  gap: 50px 100px;
  justify-content: center;
}

.project-card {
  display: flex;
  flex-direction: column;
  width: 400px;
  background: rgba(5, 0, 0, 0.3);
  box-shadow: 0 8px 32px 0 rgba(135, 47, 31, 0.37);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border: 1px 0 solid rgba(255, 255, 255, 0.18);
  border-radius: 1rem;
  transition: .2s all ease-in-out;
}

.project-card:hover {
  transform: scale(1.05);
}

.project-container .project-card .project-img img {
  border-radius: 1rem;
}

.project-title p {
  font-size: 2rem;
  text-align: center;
  font-weight: 900;
}

.project-description p {
  text-align: justify;
}

.project-links {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.social-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 20px
}

.link-container {
  background: rgba(5, 0, 0, 0.3);
  padding: 12px;
  border-radius: 10px;
  height: 100%;
  box-shadow: 0 8px 32px 0 rgba(135, 47, 31, 0.37);
}

.social-container>.link-container>a>i {
  transition: .2s all ease-in-out;
}

.social-container>.link-container>a>i:hover {
  transform: scale(1.3);
}

.contact-container {
  display: grid;
  grid-template-columns: .6fr auto;
  gap: 40px;
}

.contact-info {
  background: rgba(5, 0, 0, 0.3);
  box-shadow: 0 8px 32px 0 rgba(135, 47, 31, 0.37);

  border-radius: 1rem;
  min-height: 300px;
  padding: 3rem;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background: rgba(5, 0, 0, 0.3);
  box-shadow: 0 8px 32px 0 rgba(135, 47, 31, 0.37);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border: 1px 0 solid rgba(255, 255, 255, 0.18);
  border-radius: 1rem;
  z-index: 1;
  padding: 40px;
}

form>input,
form>textarea {
  padding: 12px;
  margin: 12px 0;
  border: none;
  color: #ddd;
  background-color: #222;
  border-radius: 4px;
  display: block;
  width: 100%;
}

form>textarea {
  height: 100px;
  resize: none;
}

.contact-btn {
  cursor: pointer;
  transition: .35s all ease-in-out;
}

.contact-btn:hover {
  background-color: #c70a0a;
  box-shadow: 0 8px 32px 0 rgba(135, 47, 31, 0.37);
}